@Injectable({
  providedIn: 'root',
})
export class UserService {

    static getUser(id: number): IUser{
      return user.filter(user => user.id == id)[0];
    }

    static getUserCount(): number{
      return;
    }

    static getUserNameList(): IStringId[]{
      let list: IStringId[] = new Array();
      user.forEach(user => {
        list.push({string: user.fname + ' ' + user.lname, id: user.id});
      });
      return list;
    }

    static updateUser(updatedUser: IUser){
      user.filter(x => {
        if (x.id == updatedUser.id)
          {
            x = updatedUser;
          }
      });
    }

}


